@import url("https://fonts.googleapis.com/css?family=Barlow+Semi+Condensed:400,400i,700,800i&display=swap");
@import url("https://fonts.googleapis.com/css?family=Barlow:500,500i&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Lora:ital@0;1&display=swap');

$theme-colour: #ff2b35;
$title-font: "Barlow Semi Condensed", sans-serif;
$body-font: "Barlow", sans-serif;
$serif-font: "Lora", Georgia, serif;
$transition: 0.2s ease;

@keyframes fadeIn {
  0%   { opacity: 0}
  100% { opacity: 1}
}

@keyframes fadeOut {
  0%   { opacity: 1}
  100% { opacity: 0}
}

@keyframes snooze {
  0%   { opacity: 0; top:0px;transform:scale(1,1);}
  25%   { opacity: 0.25;}
  100% { opacity: 0; top: -10rem;transform:scale(0,0);}
}

* {
  /*Box Sizing*/
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    -webkit-font-smoothing: auto;
    -moz-osx-font-smoothing: auto;
  }
  list-style-type: none;
  list-style-position: outside;
}
*::before,
*::after {
  box-sizing: inherit;
}

*::selection {
    background-color:rgba($theme-colour, 0.4);
}

html {
  font-size: 100%;
  @media only screen and (min-width:1200px) {
    font-size:120%;
  }
  font-family: $body-font;
  -webkit-text-size-adjust: 100%;
  line-height: 1.6em;
}

body {
  background:white;
  z-index:10;

  > header {
        font-family: "Barlow Semi Condensed", sans-serif;
        background: hsla(0,0,100, 1);
        background: -webkit-linear-gradient(hsla(0,0,100, 1) 0%, hsla(0,0,100, 1) 80%, hsla(0,0,100, 0.75) 90%, hsla(0,0,100, 0.5) 95%, hsla(0,0,100, 0) 100%);
        background: -o-linear-gradient(hsla(0,0,100, 1) 0%, hsla(0,0,100, 1) 80%, hsla(0,0,100, 0.75) 90%, hsla(0,0,100, 0.5) 95%, hsla(0,0,100, 0) 100%);
        background: linear-gradient(hsla(0,0,100, 1) 0%, hsla(0,0,100, 1) 80%, hsla(0,0,100, 0.75) 90%, hsla(0,0,100, 0.5) 95%, hsla(0,0,100, 0) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffffff",endColorstr="#ffffff",GradientType=1);
        display: flex;
        align-items: center;
        margin:auto;
        padding: 0.5rem;
        height: 3rem;
        font-size:0.85rem;
        position:fixed;
        top:0;
        width:100%;
        nav {
          width: 100%;
          max-width: 1200px;
          margin:auto;
          ul {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-evenly;
            width: 100%;
            li {
              a, a:visited {
                text-decoration: none;
                border-bottom: 1px solid #0000;
                color:#333;
                padding-bottom:0.25rem;
                &:hover {
                  border-bottom: 1px solid #000;
                }

              }
              a.current {
                color:$theme-colour;
                border-bottom: 1px solid rgba($theme-colour, 0);
                &:hover {
                  border-bottom: 1px solid $theme-colour;
                }
              }
            }
          }
        }
      }
  > main {
    min-height:calc(100vh);
    padding-top:3rem;
    padding-bottom:10rem;
    h1 {
      margin-bottom:1em;
      font-family: $title-font;
      text-align:center;
    }
    > div {
      width:90%;
      max-width:40rem;
      margin:auto;
      padding:3rem 0;
      svg {
        width:100%;
        max-width:20rem;
        margin:0 auto 1rem;
        display:flex;
      }
    }
    .blog {
      a {
        color:#666;
        text-decoration:none;
      }
      .post-snippet {
        padding-left:0.875rem;
        border-left:0.125rem solid rgba($theme-colour, 0);
        margin-bottom:3rem;
        transition: $transition;
        &:hover {
          border-left:0.125rem solid $theme-colour;
        }
        h2 {
          margin-bottom:1rem;
          font-family: $title-font;
          color:#333;
        }
        p {
          font-size:0.9rem;
          line-height:1.6em;
        }
      }
    }
    .work, .files {
      h2 {
        margin:3rem 0 1rem;
        font-family: $title-font;
        color:$theme-colour;
        &:first-of-type {
          margin-top:0;
        }
      }
      a, a:visited {
        color:#666;
        text-decoration:none;
      }
      .essay-snippet, .design-snippet, .other-snippet, .file-snippet {
        padding-left:0.875rem;
        border-left:0.125rem solid rgba($theme-colour, 0);
        margin-bottom:1rem;
        transition: $transition;
        &:hover {
          border-left:0.125rem solid $theme-colour;
        }
        h2 {
          margin:0 0 0.25rem 0;
          font-family: $title-font;
          color:#333;
        }
        p {
          font-size:0.9rem;
          line-height:1.6em;
        }
        .references {
          font-size:0.8em;
        }
      }
    }
    .cv {
      width:100%;
      max-width:none;
      padding-top:2rem;
        * {transition:0.2s ease;}
        .resumeContainer {
          min-height: 100vh;
        }
        .container {
          width: 100%;
          max-width: 60rem;
          margin: auto;
        }
        .flex {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        .break {
          width: 50%;
        }
        header {
          border-bottom: 1px solid $theme-colour;
          padding: 1rem;
          background: white;
          .title {
            display: flex;
            justify-content: center;
            flex-direction: column;
            margin:1rem 0;
            h1 {
              text-align:left;
              margin-bottom:0;
              font-size: 2.5rem;
              line-height: 2.5rem;
              font-family: $title-font;
              color: $theme-colour;
            }
            h2 {
              font-size: 1.6rem;
              line-height: 1.6rem;
              font-family: $title-font;
              font-style: italic;
              font-weight: 400;
            }
          }
          .contact-info {
            text-align: right;
            a {
              display: block;
              text-decoration: none;
              font-weight: 700;
              line-height: 2rem;
              font-family: $title-font;
              font-size: 1.2rem;
              color:#333;
              &:hover {
                color: $theme-colour;
              }
            }
          }
        }
        main {
          padding: 1rem;
          h3 {
            font-size: 2rem;
            line-height: 2rem;
            margin: 0.5rem 0;
            color: $theme-colour;
            font-family: $title-font;
            font-style: italic;
          }
          div {
            width:unset;
            max-width: none;
            margin:0;
            ul {
              li {
                margin: 1rem 0;
                h4 {
                  font-size: 1.6rem;
                  line-height: 1.2em;
                  font-family: $title-font;
                }
                .subhead {
                  font-size: 1.2rem;
                  line-height: 1.4rem;
                  font-family: $title-font;
                }
                .date {
                  text-align:right;
                  font-weight: bold;
                  line-height: 2rem;
                  font-family: $title-font;
                  i {
                    font-weight: 400;
                    font-size: 0.8rem;
                    line-height: 2rem;
                  }
                }
                .description {
                  padding: 0.5rem 0;
                }
                .contact {
                  display: inline-block;
                  padding: 0.5rem;
                  text-decoration: none;
                  font-family: $title-font;
                  font-weight: 400;
                  color:#333;
                  &:hover {
                    color: $theme-colour;
                  }
                }
              }
            }
            .skills {
              display: flex;
              ul {
                li {
                  margin: 0.75rem 0 0.75rem;
                  &:last-of-type {
                    margin-bottom: 0;
                  }
                  &::before {
                    content: "+";
                    display: inline-block;
                    color: $theme-colour;
                    padding-right: 0.5rem;
                  }
                }
              }
            }
          }
          > div {
            margin-bottom:3rem;
          }
        }

        @media screen and (max-width: 599px) {
          .flex {
            flex-direction: column;
            align-items: start;
          }
          .break {
            width: 100%;
          }
          header .contact-info {
            text-align: left;
            a {
              font-size: 1rem;
            }
          }
        }
        @media print {
          .screenOnly {
            display: none;
          }
          *,
          *::before {
            color: #333 !important;
          }
          html {
            font-size: 10pt;
          }
          *,
          *:before,
          *:after {
            border:none !important;
            font-family: sans-serif !important;
            background: none !important;
            text-shadow: none !important;
            box-shadow: none !important;
          }
        }
      }
    .about {
      color:#333;
      a, a:visited {
        color:$theme-colour;
        text-decoration:none;
        border-bottom:1px solid rgba($theme-colour, 0);
        &:hover {
          border-bottom:1px solid rgba($theme-colour, 1);
        }
      }
    }
    .essay {

      h1 {
        margin-bottom:1rem;
      }
      .essay-byline {
        text-align:center;
        font-family: $title-font;
        color:#888;
      }
      h2 {
        font-size:1.5rem;
        font-family: $serif-font;
        margin:2rem 0;
      }
      h3 {
        font-size: 1.2rem;
        font-family: $serif-font;
        margin:2rem 0;
      }
      p {
        font-family: $serif-font;
        margin-bottom:2em;
        line-height:1.75em;
        &:first-of-type::first-letter {
          initial-letter: 2;
        }
      }
      sup {
        vertical-align: super;
        line-height:1em;
        color:$theme-colour;
        font-size:0.75rem;
        position:relative;
        &:hover {
          cursor:pointer;
        }
        &:after {
          content: attr(data-tooltip);
	         position: absolute;
	          bottom: 130%;
	          left: 20%;
	          padding: 1rem;
            border-radius:0.25rem;
	          background: white;
            box-shadow:0 0 5px 0 #3333, 0 50px 100px -20px #3343, 0 30px 60px -30px #3338;
            width:15rem;
            font-size:0.9rem;
            line-height:1.4em;
            transition:0.2s ease;
            display:none;
        }
        &:hover:after {
          animation: fadeIn 0.2s ease-in;
          animation-iteration-count: 1;
          display:block;
        }
      }
      .references {
        a, a:visited {
          color:$theme-colour;
          text-decoration:none;
          border-bottom:1px solid rgba($theme-colour, 0);
          &:hover {
            border-bottom:1px solid rgba($theme-colour, 1);
          }
        }
      }

    }
    .post {
      h1 {
        margin-bottom:1rem;
      }
      p {
        margin-bottom:1rem;
      }
      a, a:visited {
        color:$theme-colour;
        text-decoration:none;
        border-bottom:1px solid rgba($theme-colour, 0);
        &:hover {
          border-bottom:1px solid rgba($theme-colour, 1);
        }
      }
      .post-byline {
        text-align:center;
        color:#888;
        font-family: $title-font;
        margin-bottom:2rem;
      }
      .post-body {
        font-family:$serif-font;
        color:#333;
      }
    }
    .file {
      h1 {
        margin-bottom:1rem;
      }
      .file-byline {
        text-align:center;
        font-family: $title-font;
        color:#888;
      }
      h2 {
        font-size:1.5rem;
        font-family: $title-font;
        margin:2rem 0;
      }
      h3 {
        font-size: 1.5rem;
        font-family: $title-font;
        margin:1rem 0;
      }
      p {
        font-family: $body-font;
        margin-bottom:2em;
        line-height:1.6em;
      }
      a, a:visited {
        color:$theme-colour;
        text-decoration:none;
        border-bottom:1px solid rgba($theme-colour, 0);
        &:hover {
          border-bottom:1px solid rgba($theme-colour, 1);
        }
      }
      > ul {
        margin-bottom:2rem;
      }
      ul {
        li {
          list-style-type: disc;
          list-style-position:outside;
          margin-bottom:0.4rem;
          line-height: 1.6em;
          ul {
            padding-left:2rem;
            li {
              list-style-type: circle;
              color:#222;
              margin-bottom:0.3rem;
              ul {
                li {
                  list-style-type:square;
                  color:#333;
                  font-size:0.9rem;
                  margin-bottom:0;
                }
              }
            }
          }
        }
      }
      ol {
        padding-left:2rem;
        li {
          list-style-type:lower-roman;
          list-style-position:outside;
        }
      }
      table {
        margin:0 auto 1rem;
      }
      th, td { border: 1px solid #aaa;padding:0.5rem;font-size:0.75rem;}
    }



    .theme-colour-background {
      background: $theme-colour;
      fill: $theme-colour;
    }
    .zzz-1, .zzz-2, .zzz-3 {
      animation: snooze 12s ease-out infinite;
      position:relative;
      transform-origin: top right;
      opacity:0;
    }
    .zzz-1 {animation-delay: 0s;}
    .zzz-2 {animation-delay: 3.5s;}
    .zzz-3 {animation-delay: 8s;}
  }
  > footer {
    background:white;
    display:flex;
    flex-direction:row;
    align-items:center;
    justify-content:space-between;
    height:80px;
    // border-top:2px dotted #888;
    box-shadow:0 -1px 5px #8883;
    color:#888;
    margin:1rem auto 0;
    font-size:0.95rem;
    padding:0 1rem;
    position:fixed;
    bottom:0;
    width:100%;
    .about {
      font-family: $title-font;
      @media only screen and (min-width:600px) {
        width:25%;
        max-width:300px;
      }
    }
    .acknowledgement {
      font-size:0.7rem;
      line-height: 1.2em;
      @media only screen and (min-width:600px) {
        width:50%;
        max-width:600px;
      }
      text-align:center;
    }
    .contact {
      display:flex;
      flex-direction:row-reverse;
      @media only screen and (min-width:600px) {
        width:25%;
        max-width:300px;
      }
      .icon {
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        height:1.5rem;
        width:1.5rem;
        display:flex;
        margin:0 .5rem;
        opacity:0.6;
        transition:0.2s ease;
        &:hover {
          opacity:1;
        }
      &.email {
        background-image: url(../img/icons/email.svg)
      }
      &.twitter {
        background-image: url(../img/icons/twitter.svg)
      }
      &.sxb {
        background-image: url(../img/icons/sxb.svg)
      }
      }
      }
      @media only screen and (max-width:600px) {
        flex-direction:column-reverse;
        height:unset;
        .break {
          margin:0.5rem auto;
          max-width:none;
        }
      }
  }
}

@media print {
  html {
    font-size:80%;
    main {
      padding:0;
      > div {
        padding:0;
      }
    }
  }
  .screen-only {
    display: none;
  }
}
